<script>
  import { fade } from "svelte/transition";
  import Gooey from "./Gooey.svelte";
  let grid = false;

  export let endpoint =
    "https://myfunscience.com/wp-json/wc/v3/products/?per_page=100&consumer_key=ck_7adf8ccaa72ed6587da5982af5744a1eae0146bf&consumer_secret=cs_02bec076137183cd1e9ca47f91307773ae77e6b6";

  async function getData() {
    const res = await fetch(endpoint);
    const products = await res.json();
    // console.log(data);
    if (res.ok) {
      return products;
    } else {
      throw new Error(products);
    }
  }
  let promise = getData();

  const toggle = () => {
    grid = !grid;
  };
</script>

<header />
<section id="fetcher" class="carbon">
  <button on:click={toggle}>Toggle</button>
  <br />
  {#await promise}
    <Gooey />
  {:then products}
    {#if grid}
      <article class="list" transition:fade>
        {#each products as product, i}
          <div>
            {#if product.status === 'publish'}
              <h3>
                <a href={product.permalink} class="" target="_blank">
                  {product.name}
                </a>
                - ${product.price}
              </h3>
            {/if}
          </div>
        {/each}
      </article>
    {:else}
      <article class="grid" transition:fade>
        {#each products as product, i}
          {#if product.status === 'publish'}
            <div class="card">
              {#each product.images as image, i}
                <img
                  width="200"
                  height="200"
                  src={image.src}
                  alt={product.name} />
              {/each}
              <h3>{product.name} - ${product.price}</h3>
              <a href={product.permalink} class="btn" target="_blank">
                Learn More
              </a>
            </div>
          {/if}
        {/each}
      </article>
    {/if}
  {:catch error}

    <p>Something has gone horribly wrong. :-(</p>
    <p style="color: red">{error.message}</p>

  {/await}

</section>
<footer />

